<template>
  <section class="withdrawals">
    <header class="withdrawals__header">
      <Heading>{{ t('treasury.withdrawals.title') }}</Heading>
    </header>
    <main class="withdrawals__container">
      <Tabs
        v-model="activeTab"
        class="withdrawals__container__tabs"
        :data="tabs"
      >
        <template #default="{ item }">
          {{ item.title }}
        </template>
      </Tabs>
      <keep-alive>
        <component
          :is="activeTab.component"
        />
      </keep-alive>
    </main>
  </section>
</template>

<script>
import Heading from '@/components/Admin/Heading'
import { useI18n } from 'vue-i18n'
import Tabs from '@/components/UIKit/Tabs'
import { computed, defineAsyncComponent, ref } from 'vue'

export default {
  name: 'Withdrawal',
  components: {
    Tabs,
    Heading,
    AddNewWithdrawal: defineAsyncComponent(() => import('@/views/admin/treasury/Withdrawals/Tabs/AddNewWithdrawal')),
    WithdrawalHistory: defineAsyncComponent(() => import('@/views/admin/treasury/Withdrawals/Tabs/WithdrawalHistory'))
  },
  setup () {
    const { t } = useI18n()
    const tabs = computed(() => [
      { title: t('treasury.withdrawals.new'), key: 'new', icon: 'plus', component: 'AddNewWithdrawal' },
      { title: t('common.history'), key: 'history', icon: 'stack', component: 'WithdrawalHistory' }
    ])
    const activeTab = ref(tabs.value[0])

    return {
      t,
      tabs,
      activeTab
    }
  }
}
</script>

<style lang="scss">
.withdrawals {
  padding: 30px 40px;

  &__header {
    margin-bottom: 40px;
  }

}
</style>
